'use client'

import * as React from "react";
import { useRouter } from 'next/navigation';
import { ROUTES } from '../../lib/router';
import { formatDate, formatDateCustom, parseDate, isDateAfter, isDateBefore, getDaysDiff } from '../../lib/date-utils';
import { useInventoryReceipts } from "./hooks/use-inventory-receipts";
import { usePurchaseOrders } from "../purchase-orders/hooks/use-purchase-orders";
import { useAllSuppliers } from "../suppliers/hooks/use-suppliers";
import { usePageHeader } from "../../contexts/page-header-context";
import { ResponsiveDataTable } from "../../components/data-table/responsive-data-table";
import { DataTableDateFilter } from "../../components/data-table/data-table-date-filter";
import { PageFilters } from "../../components/layout/page-filters";
import { PageToolbar } from "../../components/layout/page-toolbar";
import { Card, CardContent } from "../../components/ui/card";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "../../components/ui/select";
import { Button } from "../../components/ui/button";
import { useMediaQuery } from "../../lib/use-media-query";
import { Package, Calendar as CalendarIcon, Users, FileText, Printer, Download } from "lucide-react";
import Fuse from "fuse.js";
import type { ColumnDef } from "../../components/data-table/types";
import type { InventoryReceipt } from "./types";
import { Checkbox } from "../../components/ui/checkbox";
import { useAllBranches } from "../settings/branches/hooks/use-branches";
import { useStoreInfo } from "../settings/store-info/hooks/use-store-info";
import { usePrint } from "../../lib/use-print";
import { 
  convertStockInForPrint,
  mapStockInToPrintData,
  mapStockInLineItems,
  createStoreSettings,
} from "../../lib/print/stock-in-print-helper";
import { toast } from "sonner";
import { SimplePrintOptionsDialog, SimplePrintOptionsResult } from "../../components/shared/simple-print-options-dialog";
import { GenericExportDialogV2 } from "../../components/shared/generic-export-dialog-v2";
import { inventoryReceiptConfig } from "../../lib/import-export/configs/inventory-receipt.config";
import { useAuth } from "../../contexts/auth-context";
import { asSystemId } from "../../lib/id-types";
import { useColumnVisibility } from "../../hooks/use-column-visibility";

const getColumns = (
  handlers: {
    onPrint: (receipt: InventoryReceipt) => void;
  }
): ColumnDef<InventoryReceipt>[] => [
  {
    id: "select",
    header: ({ isAllPageRowsSelected, isSomePageRowsSelected, onToggleAll }) => (
      <div className="flex items-center justify-center">
        <Checkbox
          checked={isAllPageRowsSelected ? true : isSomePageRowsSelected ? "indeterminate" : false}
          onCheckedChange={(value) => onToggleAll?.(!!value)}
          aria-label="Select all"
        />
      </div>
    ),
    cell: ({ isSelected, onToggleSelect }) => (
      <div className="flex items-center justify-center">
        <Checkbox
          checked={isSelected}
          onCheckedChange={onToggleSelect}
          aria-label="Select row"
        />
      </div>
    ),
    size: 48,
    meta: {
      displayName: "Chọn",
      sticky: "left",
    },
  },
  {
    id: 'id',
    accessorKey: 'id',
    header: 'Mã phiếu',
    cell: ({ row }) => (
      <span className="font-medium text-primary">{row.id}</span>
    ),
    meta: { displayName: 'Mã phiếu' },
    size: 120,
  },
  {
    id: 'receivedDate',
    accessorKey: 'receivedDate',
    header: 'Ngày nhập',
    cell: ({ row }) => formatDateCustom(parseDate(row.receivedDate)!, 'dd/MM/yyyy HH:mm'),
    meta: { displayName: 'Ngày nhập' },
    size: 150,
  },
  {
    id: 'supplierName',
    accessorKey: 'supplierName',
    header: 'Nhà cung cấp',
    cell: ({ row }) => row.supplierName,
    meta: { displayName: 'Nhà cung cấp' },
  },
  {
    id: 'purchaseOrderId',
    accessorKey: 'purchaseOrderId',
    header: 'Đơn mua hàng',
    cell: ({ row }) => row.purchaseOrderId,
    meta: { displayName: 'Đơn mua hàng' },
    size: 140,
  },
  {
    id: 'totalQuantity',
    header: 'Tổng SL nhập',
    cell: ({ row }) => {
      const total = row.items.reduce((sum, item) => sum + Number(item.receivedQuantity), 0);
      return <span className="font-medium">{total}</span>;
    },
    meta: { displayName: 'Tổng SL nhập' },
    size: 120,
  },
  {
    id: 'receiverName',
    accessorKey: 'receiverName',
    header: 'Người nhận',
    cell: ({ row }) => row.receiverName,
    meta: { displayName: 'Người nhận' },
    size: 150,
  },
  {
    id: 'notes',
    accessorKey: 'notes',
    header: 'Ghi chú',
    cell: ({ row }) => (
      <span className="text-body-xs max-w-xs line-clamp-2">
        {row.notes || '-'}
      </span>
    ),
    meta: { displayName: 'Ghi chú' },
    size: 200,
  },
  {
    id: 'itemsCount',
    header: 'Số mặt hàng',
    cell: ({ row }) => (
      <span className="font-medium">{row.items.length}</span>
    ),
    meta: { displayName: 'Số mặt hàng' },
    size: 120,
  },
  {
    id: 'productNames',
    header: 'Sản phẩm',
    cell: ({ row }) => {
      const firstProduct = row.items[0]?.productName || '';
      const remaining = row.items.length - 1;
      return (
        <span className="text-body-xs">
          {firstProduct}
          {remaining > 0 && ` +${remaining}`}
        </span>
      );
    },
    meta: { displayName: 'Sản phẩm' },
    size: 200,
  },
  {
    id: 'totalValue',
    header: 'Tổng giá trị',
    cell: ({ row }) => {
      const total = row.items.reduce((sum, item) => 
        sum + (item.receivedQuantity * item.unitPrice), 0
      );
      return (
        <span className="font-semibold">
          {new Intl.NumberFormat('vi-VN', { style: 'currency', currency: 'VND' }).format(total)}
        </span>
      );
    },
    meta: { displayName: 'Tổng giá trị' },
    size: 150,
  },
  {
    id: 'actions',
    header: 'Hành động',
    cell: ({ row }) => (
      <Button
        variant="ghost"
        size="sm"
        className="h-8"
        onClick={(e) => {
          e.stopPropagation();
          handlers.onPrint(row);
        }}
      >
        <Printer className="h-4 w-4 mr-1" />
        In
      </Button>
    ),
    size: 120,
    meta: {
      displayName: 'Hành động',
      sticky: 'right',
    },
  },
];

export function InventoryReceiptsPage() {
  // React Query hooks
  const { data: receiptsData } = useInventoryReceipts();
  const receipts = receiptsData?.data || [];
  const { data: purchaseOrdersData } = usePurchaseOrders();
  const allPurchaseOrders = purchaseOrdersData?.data || [];
  const { data: suppliersData } = useAllSuppliers();
  const suppliers = suppliersData || [];
  const { data: branchesData } = useAllBranches();
  const branches = branchesData?.data || [];
  const { data: storeInfoData } = useStoreInfo();
  const storeInfo = storeInfoData || null;
  
  // Helper functions
  const findSupplierById = React.useCallback((systemId: string) => {
    return suppliers.find(s => s.systemId === systemId);
  }, [suppliers]);
  
  const findBranchById = React.useCallback((systemId: string) => {
    return branches.find(b => b.systemId === systemId);
  }, [branches]);
  
  const { print, printMultiple } = usePrint();
  const { employee: currentUser } = useAuth();
  const router = useRouter();
  const isMobile = useMediaQuery("(max-width: 768px)");
  
  // Export dialog state
  const [exportDialogOpen, setExportDialogOpen] = React.useState(false);

  const headerActions = React.useMemo(() => [
    <Button
      key="purchase-orders"
      variant="outline"
      size="sm"
      className="h-9"
      onClick={() => router.push(ROUTES.PROCUREMENT.PURCHASE_ORDERS)}
    >
      Đơn mua hàng
    </Button>,
    <Button
      key="create"
      size="sm"
      className="h-9"
      onClick={() => router.push(ROUTES.PROCUREMENT.PURCHASE_ORDER_NEW)}
    >
      Tạo phiếu nhập
    </Button>,
  ], [router]);
  
  usePageHeader({
    title: 'Danh sách phiếu nhập kho',
    breadcrumb: [
      { label: 'Trang chủ', href: ROUTES.DASHBOARD, isCurrent: false },
      { label: 'Phiếu nhập kho', href: ROUTES.PROCUREMENT.INVENTORY_RECEIPTS, isCurrent: true }
    ],
    showBackButton: false,
    actions: headerActions
  });

  // Không cần combine nữa, chỉ dùng receipts
  const filteredDataBase = React.useMemo(() => {
    return [...receipts].sort((a, b) => 
      getDaysDiff(parseDate(b.receivedDate), parseDate(a.receivedDate))
    );
  }, [receipts]);

  const [rowSelection, setRowSelection] = React.useState<Record<string, boolean>>({});
  const [sorting, setSorting] = React.useState<{ id: string, desc: boolean }>({ id: 'createdAt', desc: true });
  const [globalFilter, setGlobalFilter] = React.useState('');
  const [debouncedGlobalFilter, setDebouncedGlobalFilter] = React.useState('');
  const [supplierFilter, setSupplierFilter] = React.useState('all');
  const [branchFilter, setBranchFilter] = React.useState('all');
  const [dateRange, setDateRange] = React.useState<[string | undefined, string | undefined] | undefined>();
  const [pagination, setPagination] = React.useState({ pageIndex: 0, pageSize: 20 });
  
  // Use hook for column visibility (database-backed)
  const [columnVisibility, setColumnVisibility] = useColumnVisibility('inventory-receipts', {});
  
  const [columnOrder, setColumnOrder] = React.useState<string[]>([]);
  const [pinnedColumns, setPinnedColumns] = React.useState<string[]>([]);
  const [expanded, setExpanded] = React.useState<Record<string, boolean>>({});
  const [mobileLoadedCount, setMobileLoadedCount] = React.useState(20);

  React.useEffect(() => {
    const timer = setTimeout(() => {
      setDebouncedGlobalFilter(globalFilter);
    }, 300);
    return () => clearTimeout(timer);
  }, [globalFilter]);

  const handlePrintReceipt = React.useCallback((receipt: InventoryReceipt) => {
    // Use helper to prepare print data
    const branch = findBranchById(receipt.branchSystemId);
    const supplier = findSupplierById(receipt.supplierSystemId);
    const storeSettings = branch 
      ? createStoreSettings(branch)
      : createStoreSettings(storeInfo);
    const receiptData = convertStockInForPrint(receipt, { branch, supplier });
    
    print('stock-in', {
      data: mapStockInToPrintData(receiptData, storeSettings),
      lineItems: mapStockInLineItems(receiptData.items),
    });
    toast.info('Đang gửi lệnh in', {
      description: `Phiếu ${receipt.id} - ${receipt.supplierName}`
    });
  }, [findBranchById, findSupplierById, storeInfo, print]);

  const columns = React.useMemo(() => getColumns({ onPrint: handlePrintReceipt }), [handlePrintReceipt]);
  
  React.useEffect(() => {
    if (!columns.length) return;
    const next = { ...columnVisibility };
    let changed = false;
    columns.forEach(c => {
      if (!c.id) return;
      if (typeof next[c.id] === 'undefined') {
        next[c.id] = true;
        changed = true;
      }
    });
    if (changed) {
      setColumnVisibility(next);
    }
    if (columnOrder.length === 0) {
      setColumnOrder(columns.map(c => c.id).filter(Boolean) as string[]);
    }
  }, [columns]);
  
  const fuse = React.useMemo(
    () => new Fuse(filteredDataBase, { 
      keys: ["id", "supplierName", "receiverName", "purchaseOrderId"],
      threshold: 0.3,
      ignoreLocation: true
    }), 
    [filteredDataBase]
  );
  
  const filteredData = React.useMemo(() => {
    let data = filteredDataBase;

    if (supplierFilter !== 'all') {
      data = data.filter(v => v.supplierSystemId === supplierFilter);
    }

    if (branchFilter !== 'all') {
      data = data.filter(v => v.branchSystemId === branchFilter);
    }

    if (dateRange && (dateRange[0] || dateRange[1])) {
      data = data.filter(v => {
        const voucherDate = parseDate(v.receivedDate);
        if (!voucherDate) return false;
        
        const fromDate = dateRange[0] ? new Date(dateRange[0]) : null;
        const toDate = dateRange[1] ? new Date(dateRange[1]) : null;
        
        if (fromDate && isDateBefore(voucherDate, fromDate)) return false;
        if (toDate && isDateAfter(voucherDate, toDate)) return false;
        return true;
      });
    }

    if (debouncedGlobalFilter) {
      const searchResults = fuse.search(debouncedGlobalFilter);
      return searchResults.map(r => r.item);
    }

    return data;
  }, [filteredDataBase, supplierFilter, branchFilter, dateRange, debouncedGlobalFilter, fuse]);

  React.useEffect(() => {
    setMobileLoadedCount(20);
  }, [debouncedGlobalFilter, supplierFilter, branchFilter, dateRange]);

  React.useEffect(() => {
    if (!isMobile) return;

    const handleScroll = () => {
      const scrollTop = window.scrollY || document.documentElement.scrollTop;
      const scrollHeight = document.documentElement.scrollHeight;
      const clientHeight = document.documentElement.clientHeight;

      const filteredCount = filteredData.length;
      if (scrollTop + clientHeight >= scrollHeight * 0.8 && mobileLoadedCount < filteredCount) {
        setMobileLoadedCount(prev => Math.min(prev + 20, filteredCount));
      }
    };

    window.addEventListener('scroll', handleScroll, { passive: true });
    return () => window.removeEventListener('scroll', handleScroll);
  }, [isMobile, mobileLoadedCount, filteredData]);
  
  const sortedData = React.useMemo(() => {
    let sorted = [...filteredData];
    if (sorting && sorting.id) {
      sorted.sort((a, b) => {
        const aVal = (a as any)[sorting.id];
        const bVal = (b as any)[sorting.id];
        // Special handling for date columns
        if (sorting.id === 'createdAt' || sorting.id === 'receivedDate') {
          const aTime = aVal ? new Date(aVal).getTime() : 0;
          const bTime = bVal ? new Date(bVal).getTime() : 0;
          return sorting.desc ? bTime - aTime : aTime - bTime;
        }
        if (aVal === bVal) return 0;
        if (aVal < bVal) return sorting.desc ? 1 : -1;
        if (aVal > bVal) return sorting.desc ? -1 : 1;
        return 0;
      });
    }
    return sorted;
  }, [filteredData, sorting]);

  const pageCount = Math.ceil(sortedData.length / pagination.pageSize);
  const paginatedData = React.useMemo(() => {
    const start = pagination.pageIndex * pagination.pageSize;
    const end = start + pagination.pageSize;
    return sortedData.slice(start, end);
  }, [sortedData, pagination]);

  const handleRowClick = React.useCallback((row: InventoryReceipt) => {
    router.push(ROUTES.PROCUREMENT.INVENTORY_RECEIPT_VIEW.replace(':systemId', row.systemId));
  }, [router]);

  const selectedRows = React.useMemo(() => {
    return sortedData.filter(r => rowSelection[r.systemId]);
  }, [sortedData, rowSelection]);

  // Print dialog state
  const [isPrintDialogOpen, setIsPrintDialogOpen] = React.useState(false);
  const [pendingPrintReceipts, setPendingPrintReceipts] = React.useState<InventoryReceipt[]>([]);

  // Open print options dialog
  const handleBulkPrint = React.useCallback(() => {
    if (selectedRows.length === 0) return;
    setPendingPrintReceipts(selectedRows);
    setIsPrintDialogOpen(true);
  }, [selectedRows]);

  // Xử lý khi xác nhận tùy chọn in từ dialog
  const handlePrintConfirm = React.useCallback((options: SimplePrintOptionsResult) => {
    const { branchSystemId, paperSize } = options;
    
    // Chuẩn bị danh sách options cho printMultiple
    const printOptionsList = pendingPrintReceipts.map(receipt => {
      // Ưu tiên dùng chi nhánh user chọn, nếu không thì dùng chi nhánh của phiếu
      const branch = branchSystemId 
        ? findBranchById(branchSystemId)
        : findBranchById(receipt.branchSystemId);
      const supplier = findSupplierById(receipt.supplierSystemId);
      const storeSettings = branch 
        ? createStoreSettings(branch)
        : createStoreSettings(storeInfo);
      const receiptData = convertStockInForPrint(receipt, { branch, supplier });
      
      return {
        data: mapStockInToPrintData(receiptData, storeSettings),
        lineItems: mapStockInLineItems(receiptData.items),
        paperSize,
      };
    });
    
    // In tất cả trong 1 lần (1 popup) bằng printMultiple
    printMultiple('stock-in', printOptionsList);
    
    toast.success('Đã gửi lệnh in cho phiếu nhập', {
      description: pendingPrintReceipts.map(r => r.id).join(', ')
    });
    setRowSelection({});
    setPendingPrintReceipts([]);
  }, [pendingPrintReceipts, findBranchById, findSupplierById, storeInfo, printMultiple]);

  const bulkActions = [
    {
      label: "In phiếu nhập",
      icon: Printer,
      onSelect: handleBulkPrint
    }
  ];

  const receiptStats = React.useMemo(() => {
    const totalReceipts = filteredData.length;
    const totalQuantity = filteredData.reduce((sum, r) => {
      return sum + r.items.reduce((itemSum, item) => itemSum + item.receivedQuantity, 0);
    }, 0);
    return { totalReceipts, totalQuantity };
  }, [filteredData]);

  const supplierOptions = React.useMemo(() => {
    const supplierMap = new Map<string, string>();
    filteredDataBase.forEach(r => {
      if (r.supplierSystemId) {
        supplierMap.set(r.supplierSystemId, r.supplierName);
      }
    });

    suppliers.forEach(s => {
      if (filteredDataBase.some(r => r.supplierSystemId === s.systemId)) {
        supplierMap.set(s.systemId, s.name);
      }
    });

    return Array.from(supplierMap.entries())
      .map(([value, label]) => ({ value, label }))
      .sort((a, b) => a.label.localeCompare(b.label));
  }, [filteredDataBase, suppliers]);

  const branchOptions = React.useMemo(() => {
    const branchMap = new Map<string, string>();
    filteredDataBase.forEach(r => {
      if (r.branchSystemId) {
        branchMap.set(r.branchSystemId, r.branchName || branches.find(b => b.systemId === r.branchSystemId)?.name || 'Chưa gắn chi nhánh');
      }
    });

    return Array.from(branchMap.entries())
      .map(([value, label]) => ({ value, label }))
      .sort((a, b) => a.label.localeCompare(b.label));
  }, [filteredDataBase, branches]);

  const MobileReceiptCard = ({ receipt }: { receipt: InventoryReceipt }) => {
    const totalQuantity = receipt.items.reduce((sum, item) => sum + item.receivedQuantity, 0);
    
    return (
      <Card className="mb-4 cursor-pointer hover:border-primary" onClick={() => handleRowClick(receipt)}>
        <CardContent className="p-4">
          <div className="flex items-start justify-between">
            <div className="flex items-start gap-3">
              <div className="p-2 rounded-full bg-green-100 text-green-600">
                <Package className="h-6 w-6" />
              </div>
              <div className="flex-1">
                <div className="font-semibold text-body-sm mb-1">{receipt.id}</div>
                <div className="text-body-sm text-muted-foreground space-y-1">
                  <div className="flex items-center gap-1">
                    <CalendarIcon className="h-3 w-3" />
                    <span>{formatDate(receipt.receivedDate)}</span>
                  </div>
                  <div className="flex items-center gap-1">
                    <Users className="h-3 w-3" />
                    <span>{receipt.supplierName}</span>
                  </div>
                  {receipt.purchaseOrderId && (
                    <div className="flex items-center gap-1">
                      <FileText className="h-3 w-3" />
                      <span>Đơn: {receipt.purchaseOrderId}</span>
                    </div>
                  )}
                  {receipt.receiverName && (
                    <div className="flex items-center gap-1">
                      <Users className="h-3 w-3" />
                      <span>Người nhận: {receipt.receiverName}</span>
                    </div>
                  )}
                </div>
              </div>
            </div>
            <div className="text-right">
              <div className="font-semibold text-body-sm">
                {totalQuantity} SP
              </div>
            </div>
          </div>
        </CardContent>
      </Card>
    );
  };

  return (
    <div className="space-y-4 flex flex-col h-full">
      {/* PageToolbar - Desktop only */}
      {!isMobile && (
        <PageToolbar
          leftActions={
            <Button variant="outline" size="sm" onClick={() => setExportDialogOpen(true)}>
              <Download className="h-4 w-4 mr-2" />
              Xuất Excel
            </Button>
          }
        />
      )}

      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4">
        <Card>
          <CardContent className="p-4">
            <p className="text-body-sm text-muted-foreground">Tổng phiếu nhập</p>
            <p className="text-h3 font-semibold text-green-600">{receiptStats.totalReceipts}</p>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="p-4">
            <p className="text-body-sm text-muted-foreground">Tổng số lượng nhập</p>
            <p className="text-h3 font-semibold text-blue-600">{receiptStats.totalQuantity} SP</p>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="p-4">
            <p className="text-body-sm text-muted-foreground">Đang hiển thị</p>
            <p className="text-h3 font-semibold">{filteredData.length} / {receipts.length}</p>
          </CardContent>
        </Card>
      </div>

      <PageFilters
        searchValue={globalFilter}
        onSearchChange={setGlobalFilter}
        searchPlaceholder="Tìm theo mã phiếu, NCC, đơn hàng..."
      >
        <Select value={supplierFilter} onValueChange={setSupplierFilter}>
          <SelectTrigger className="h-9 w-full sm:w-[200px]">
            <SelectValue placeholder="Nhà cung cấp" />
          </SelectTrigger>
          <SelectContent>
            <SelectItem value="all">Tất cả NCC</SelectItem>
            {supplierOptions.map(opt => (
              <SelectItem key={opt.value} value={opt.value}>{opt.label}</SelectItem>
            ))}
          </SelectContent>
        </Select>

        <Select value={branchFilter} onValueChange={setBranchFilter}>
          <SelectTrigger className="h-9 w-full sm:w-[200px]">
            <SelectValue placeholder="Chi nhánh" />
          </SelectTrigger>
          <SelectContent>
            <SelectItem value="all">Tất cả chi nhánh</SelectItem>
            {branchOptions.map(opt => (
              <SelectItem key={opt.value} value={opt.value}>{opt.label}</SelectItem>
            ))}
          </SelectContent>
        </Select>

        <DataTableDateFilter
          value={dateRange}
          onChange={setDateRange}
          title="Ngày nhập hàng"
        />
      </PageFilters>

      {isMobile ? (
        <div className="space-y-4">
          <div className="space-y-2">
            {sortedData.length === 0 ? (
              <Card>
                <CardContent className="p-8 text-center text-muted-foreground">
                  Không tìm thấy phiếu nào.
                </CardContent>
              </Card>
            ) : (
              sortedData.slice(0, mobileLoadedCount).map((r) => (
                <MobileReceiptCard key={r.systemId} receipt={r} />
              ))
            )}
          </div>

          {sortedData.length > 0 && (
            <div className="py-6 text-center">
              {mobileLoadedCount < sortedData.length ? (
                <div className="flex items-center justify-center gap-2 text-muted-foreground">
                  <div className="h-4 w-4 animate-spin rounded-full border-2 border-primary border-t-transparent" />
                  <span className="text-body-sm">Đang tải thêm...</span>
                </div>
              ) : sortedData.length > 20 ? (
                <p className="text-body-sm text-muted-foreground">
                  Đã hiển thị tất cả {sortedData.length} phiếu
                </p>
              ) : null}
            </div>
          )}
        </div>
      ) : (
        <ResponsiveDataTable
          columns={columns}
          data={paginatedData}
          renderMobileCard={(row) => <MobileReceiptCard receipt={row} />}
          pageCount={pageCount}
          pagination={pagination}
          setPagination={setPagination}
          rowCount={sortedData.length}
          rowSelection={rowSelection}
          setRowSelection={setRowSelection}
          allSelectedRows={selectedRows}
          bulkActions={bulkActions}
          showBulkDeleteButton={false}
          expanded={expanded}
          setExpanded={setExpanded}
          sorting={sorting}
          setSorting={setSorting}
          columnVisibility={columnVisibility}
          setColumnVisibility={setColumnVisibility}
          columnOrder={columnOrder}
          setColumnOrder={setColumnOrder}
          pinnedColumns={pinnedColumns}
          setPinnedColumns={setPinnedColumns}
          onRowClick={handleRowClick}
        />
      )}

      {/* Print Options Dialog */}
      <SimplePrintOptionsDialog
        open={isPrintDialogOpen}
        onOpenChange={setIsPrintDialogOpen}
        onConfirm={handlePrintConfirm}
        selectedCount={pendingPrintReceipts.length}
        title="In phiếu nhập kho"
      />

      {/* Export Dialog */}
      <GenericExportDialogV2<InventoryReceipt>
        open={exportDialogOpen}
        onOpenChange={setExportDialogOpen}
        config={inventoryReceiptConfig}
        allData={receipts}
        filteredData={filteredData}
        currentPageData={paginatedData}
        selectedData={selectedRows}
        currentUser={{
          name: currentUser?.fullName || 'Hệ thống',
          systemId: currentUser?.systemId || asSystemId('SYSTEM'),
        }}
      />
    </div>
  );
}